
var React = require('react');

var KeywordSearch = React.createClass({displayName: "KeywordSearch",

    _onKeyUp: function(ev) {
        var value = $(ev.target).val();
        var length = value.length;
        // filter the items only if we have at least 3 characters
        if (length > 2 || ev.keyCode == 13) {
            this.props.onSearch(value);
        } else {
            this.props.onSearch();
        }
    },

    render: function() {
        return(
              React.createElement("div", {className: "text-search-wrapper"}, 
                  React.createElement("div", {className: "search-box"}, 
                  React.createElement("button", {type: "submit", className: "search-button", onClick: this.handleCLick}, 
          					React.createElement("i", {className: "fa fa-search"})
          				), 
                      React.createElement("input", {className: "keyword-search", placeholder: "Search filters", name: "keyword-search", type: "text", onKeyUp: this._onKeyUp})
                  )
              )
            );
    }
});

module.exports = KeywordSearch;
