'use strict';

var React = require('react/addons')
var Reflux = require('reflux');
var MapActions=require('../../actions/mapActions.js')
var MapStore=require('../../stores/mapStore.js');

var BaseMapItem= React.createClass({displayName: "BaseMapItem",

   handleClick: function(event) {
     MapActions.changeBaseMap(this.state.value);
  },

   getInitialState: function() {
      return {value:this.props.value, label:this.props.label};
  },
  
  render: function() {
    var currentBaseMap = MapStore.getCurrentBaseMap();
    var cls = currentBaseMap==this.state.value? "basemap-option-selected" : "basemap-option";
    return(React.createElement("span", {className: cls, onClick: this.handleClick}, " ", this.state.label, " "));
  }

});


module.exports = React.createClass({displayName: "exports",

  mixins: [Reflux.connect(MapStore)],
  
  render: function() {
    return(
        React.createElement("div", {className: "map-panel-header"}, 
          React.createElement("div", {className: "basemap-label"}, 
            React.createElement("i", {className: "fa fa-globe", "aria-hidden": "true"}), 
            "Views"
          ), 
          React.createElement("ul", {className: "basemap-list"}, 
            React.createElement("li", null, React.createElement(BaseMapItem, {label: "Gray", value: "Gray"})), 
            React.createElement("li", null, React.createElement(BaseMapItem, {label: "Topographic", value: "Topographic"})), 
            React.createElement("li", null, React.createElement(BaseMapItem, {label: "National Geographic", value: "NationalGeographic"})), 
            React.createElement("li", null, React.createElement(BaseMapItem, {label: "Dark Gray", value: "DarkGray"})), 
            React.createElement("li", null, React.createElement(BaseMapItem, {label: "Imagery", value: "Imagery"})), 
            React.createElement("li", null, React.createElement(BaseMapItem, {label: "Streets", value: "Streets"}))
          )
        )
      );
  }
});
