
'use strict';

/*http://facebook.github.io/react/docs/component-specs.html*/
var React = require('react');
var CustomCheckbox = require('../../commons/customCheckbox.jsx');

var FilterItem = React.createClass({displayName: "FilterItem",
 
    _onItemChanged: function(value, selected) {  
        selected = !this.state.selected; 
        this.setState({selected: selected});
        this.props.onItemChanged(this.props.filterType, value, selected);
    },
 
    _onLabelClicked: function(e) { 
        var selected = !this.state.selected; 
        this.setState({selected: selected});
        this.props.onItemChanged(this.props.filterType, $(e.currentTarget).data('id'), selected);
    },
 
    getInitialState: function() {  
       return {selected: this.props.selected};
    },
 
    componentWillReceiveProps: function(newProps) {  
       this.setState({selected: newProps.selected});
    },

    render: function() {
        //console.log("filters->filter-item: render");
        var childSelectedCount = "";
        var className = "filter-col";
        var collapseDiv = "";
        var label = "";
        if (this.props.onExpandCollapse){
            className = "filter-col-parent";
            collapseDiv = React.createElement("div", {className: "parent-toggle", onClick: this.props.onExpandCollapse}, 
                            React.createElement("span", null, this.props.collapsed? "expand" : "collapse"), 
                            React.createElement("span", {className: this.props.collapsed? "collapse-icon fa fa-plus" : "collapse-icon fa fa-minus"})
                        );
        }
        if (this.props.childSelectedCount){
            childSelectedCount = React.createElement("div", {className: "children-count"}, 
                                    "(", this.props.childSelectedCount, ")"
                                );
        }
        if (this.state.selected){
            label = React.createElement("span", {title: this.props.name, 
                        onClick: this._onLabelClicked, 
                        "data-id": this.props.id, 
                        "data-placement": "top", 
                        className: "item-label label-selected"}, 
                            this.props.name
                    );
        } else {
            label = React.createElement("span", {title: this.props.name, 
                        onClick: this._onLabelClicked, 
                        "data-id": this.props.id, 
                        "data-placement": "top", 
                        className: "item-label"}, 
                        this.props.name
                    );
        }
        return(
        React.createElement("div", {className: className}, 
            React.createElement(CustomCheckbox, {
                    selected: this.state.selected, 
                    onChange: this._onItemChanged, 
                    value: this.props.id}), 
            label, 
            childSelectedCount, 
            collapseDiv
        )
        );
    }
});

module.exports = FilterItem;