var React = require('react');

var AllNoneSelector = React.createClass({displayName: "AllNoneSelector",

    _allSelected: function() {
        this.props.onAllNoneClicked(this.props.filterType, true);
    },

    _noneSelected: function() {
        this.props.onAllNoneClicked(this.props.filterType, false);
    },

    render: function() {
        return(
              React.createElement("div", {className: "filter-selectors"}, 
                  React.createElement("span", null, React.createElement("a", {href: "#", onClick: this._allSelected}, "select all")), " / ", React.createElement("span", null, React.createElement("a", {href: "#", onClick: this._noneSelected}, "deselect all"))
              )
            );
    }
});

module.exports = AllNoneSelector;
