'use strict';

var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;

var LayersAction=require('../../../../actions/layersAction.js');
var DataLayerStore=require('../../../../stores/dataLayerStore.js');
var RadioButton=require('../../../commons/customRadioButton.jsx');


module.exports  = React.createClass({displayName: "exports",
 mixins: [Reflux.connect(DataLayerStore)], 

	showByDepartment:function(){
		console.log('layers->dataLayerSelector: ShowByDepartment');		
		if (this.state.dataLayer=='indicator'){
			LayersAction.loadIndicatorsByDepartments();
		} else {
			LayersAction.loadActivitiesByDepartments();
		}
	},

	showByMunicipality:function(){
		console.log('layers->dataLayerSelector: showByMunicipality');
		if (this.state.dataLayer=='indicator'){
			LayersAction.loadIndicatorsByMuncipalities();
		} else {
			LayersAction.loadActivitiesByMuncipalities();
		}
	},

	enableFinancingSelector:function(){
		if (this.state.dataLevel=='departament'){
			LayersAction.loadActivitiesByDepartments();
		} else {
			LayersAction.loadActivitiesByMuncipalities();
		}
	},

	enableIndicatorSelector:function(){
		if (this.state.dataLevel=='departament'){
			LayersAction.loadIndicatorsByDepartments();
		} else {
			LayersAction.loadIndicatorsByMuncipalities();
		}
	},

	componentDidMount :function(){ 
		console.log('layers->manager: componentDidMount');
    },

    render: function() {
    	console.log('layers->dataLayerSelector: Render');
    	var dataLayer = this.state.dataLayer;
		var dataLevel = this.state.dataLevel;
		var finSelectorClass = dataLayer=='funding'? "" : "disabled";
		var indSelectorClass = dataLayer=='indicator'? "" : "disabled";
		return (  
		React.createElement("div", {className: "inline"}, 
			React.createElement("div", {className: ""}, 
				React.createElement(RadioButton, {name: "dataLayer", label: "Financing", selected: dataLayer=='funding'? true : false, onClick: this.enableFinancingSelector}), 
				React.createElement("div", {className: finSelectorClass}, 
					React.createElement("ul", null, 
						React.createElement("li", null, 
							React.createElement(RadioButton, {name: "finLevel", selected: (dataLayer=='funding'&&dataLevel=='departament')? true : false, onClick: this.showByDepartment, label: "layers.byDepartment"}), 
							React.createElement(RadioButton, {name: "finLevel", selected: (dataLayer=='funding'&&dataLevel=='municipality')? true : false, onClick: this.showByMunicipality, label: "layers.byMunicipality"})	
						)
					)					
				)
			), 	
			React.createElement("div", {className: ""}, 
				React.createElement(RadioButton, {name: "dataLayer", label: "Indicators", selected: dataLayer=='indicator'? true : false, onClick: this.enableIndicatorSelector}), 
				React.createElement("div", {className: indSelectorClass}, 
					React.createElement("ul", null, 
						React.createElement("li", null, 
							React.createElement(RadioButton, {name: "indLevel", selected: (dataLayer=='indicator'&&dataLevel=='departament')? true : false, onClick: this.showByDepartment, label: "layers.byDepartment"}), 
							React.createElement(RadioButton, {name: "indLevel", selected: (dataLayer=='indicator'&&dataLevel=='municipality')? true : false, onClick: this.showByMunicipality, label: "layers.byMunicipality"})
						)
					)
				)
			)	
		)   
		);
	}

});