'use strict';
var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;

var ArcgisLayersActions=require('../../../../actions/arcgisLayersActions.js');
var Results=require('./results.jsx');
var _=require('lodash');
var CustomCheckbox = require('../../../commons/customCheckbox.jsx');


var SearchInput=React.createClass({displayName: "SearchInput",

	handleCLick:function(){
		this.setState(_.assign(this.state,{"query":this.refs.search_input.getDOMNode().value}));
		this.props.onSearch(this.state);
	},

	handleOnkeypress:function(key){
		if(key.which==13){
			this.handleCLick();
		}

	},

	getInitialState: function() {
		return {'feature': true,'image':true,'map':true ,'start':0 ,'num':20};
	},

	checkOption:function(value, selected){
		var newState={};
		newState[value]=selected;
 		this.setState(newState);
	},


	render: function() {
		console.log("layers->search->search: Render EsriSearch");
	return(

				React.createElement("div", {className: "layer-search-wrapper"}, 
					React.createElement("div", {className: "text-search-wrapper"}, 
						React.createElement("div", {className: "search-box"}, 
							React.createElement("button", {type: "submit", className: "search-button", onClick: this.handleCLick}, 
								React.createElement("i", {className: "fa fa-search"})
							), 
							React.createElement("input", {onKeyPress: this.handleOnkeypress, className: "keyword-search", type: "text", placeholder: "Search layer", ref: "search_input"})
						)
					), 
					React.createElement("div", {className: "layer-search-options"}, 
						React.createElement("ul", null, 

							React.createElement("li", null, 
								React.createElement(CustomCheckbox, {selected: this.state.feature, onChange: this.checkOption, value: "feature"}), 
								"Feature Service"
							), 
							React.createElement("li", null, 
								React.createElement(CustomCheckbox, {selected: this.state.map, onChange: this.checkOption, value: "map"}), 
								"Map Service"
							), 
							React.createElement("li", null, 
								React.createElement(CustomCheckbox, {selected: this.state.image, onChange: this.checkOption, value: "image"}), 
								"Image Service"
							)
						)			
					)
				));

	}
		
});


var NoResutsMessage=React.createClass(
{displayName: "NoResutsMessage",
	render:function(){
		return React.createElement("div", {className: "bs-callout bs-callout-info", id: "callout-help-text-accessibility"}, 
		React.createElement("p", null, "Use input search for finding Arcgis Online Layers and add them to your ", React.createElement("code", null, "Map"))
		)
	}
}
);

/*
  Root Element input text + search list
  */



  module.exports  = React.createClass({displayName: "exports",

  	handleNextPage:function(){
  		if (this.state){
  			var newState=_.assign({},this.state,{'start':this.props.search.nextStart,'append':true});
 			this.setState(newState);
 			this.props.onSearch(newState);
  		}
  	},

  	onSearch:function(val){
  		var newState=_.assign(val,{'append':false});
  		this.setState(newState);
  		this.props.onSearch(newState);
  	},




  	render: function() {
  		
  		console.log("layers->search->search: Render Layer Search");
  		return (
  			React.createElement("div", null, 
  			React.createElement(SearchInput, {onSearch: this.onSearch}), 
  			React.createElement("hr", {className: "h-divider"}), 
  			this.props.search && this.props.search.results?
  				React.createElement(Results, {
  				onNextPage: this.handleNextPage, 
  				search: this.props.search, 
  				onAddLayer: this.props.onAddLayer, 
  				token: this.props.token, 
  				error: this.props.error}):React.createElement(NoResutsMessage, null)
  				
  				)
  				);
  	}
  });
