
/*http://facebook.github.io/react/docs/component-specs.html*/
var React = require('react');
var Router = require('react-router');
var Reflux = require('reflux');
var Link = Router.Link;

var FilterButtons = React.createClass({displayName: "FilterButtons",

    render: function() {
        var item = this.props.data;
        return(
            React.createElement("div", {className: "button-pane"}, 
              React.createElement("button", {type: "button", className: "btn btn-apply", role: "button", onClick: this.props.onClickReset}, "Reset"), 
              React.createElement("button", {type: "button", className: "btn btn-apply space-left", role: "button", onClick: this.props.onClickApply}, "Apply")
            )
        );
    }
});

module.exports = FilterButtons;
