
'use strict';
var React = require('react')
var LanActions=require('../actions/lanActions.js');
var LanStore=require('../stores/lanStore.js');
var Reflux = require('reflux');



module.exports = React.createClass({displayName: "exports",

	mixins: [Reflux.connect(LanStore, 'lan')],

	handleLocaleChange:function(event){
		var target = $( event.currentTarget );
   		target.closest('.language-btn').find('[data-bind="label"]').text(target.text());
       	LanActions.changeLocale(target.data("value"));
	},
	

	render: function() {
		return (
			React.createElement("div", {className: "language-btn"}, 
	            React.createElement("button", {type: "button", className: "btn btn-language-select dropdown-toggle", "data-toggle": "dropdown"}, 
	              React.createElement("span", {"data-bind": "label"}, "Language"), " ", React.createElement("span", {className: "caret"})
	            ), 
	            React.createElement("ul", {className: "dropdown-menu", role: "menu"}, 
	              React.createElement("li", {onClick: this.handleLocaleChange, "data-value": "en"}, React.createElement("a", {href: "#"}, "English")), 
	              React.createElement("li", {onClick: this.handleLocaleChange, "data-value": "es"}, React.createElement("a", {href: "#"}, "Spanish"))
	            )
          	)
			);
	}
});


